#productList .content {
  background: #F1F1F1;
}
#productList .content .left {
    width: 200px;
    height: 530px;
    background: #fff;
    overflow-y: auto;
}
#productList .content .right {
    width: 970px;
}
#productList .content .right .nodata {
      height: 430px;
      text-align: center;
      line-height: 430px;
}
#productList .content .right .page-num {
      padding: 10px 0;
}
#productList .content .right .page-num strong.title {
        color: #666666;
}
#productList .content .right .page-num .pagebtn {
        margin-right: 20px;
}
#productList .content .right .page-num .pagebtn .cpage {
          color: #FF4400;
}
#productList .content .right .page-num .arrowout {
        cursor: pointer;
        height: 25px;
        width: 48px;
        border: 1px solid #FF4400;
}
#productList .content .right .page-num .arrowout .arrow-r,
        #productList .content .right .page-num .arrowout .arrow-l {
          border-color: #FF4400;
}
#productList .content .right .page-num .arrowout.brn {
        border-right: none;
}
#productList .content .right .store-bar {
      height: 40px;
      background: #fff;
      padding: 0 10px;
}
#productList .content .right .store-bar .status {
        width: 82px;
        height: 24px;
        background: #FAB715;
        color: #fff;
        font-size: 12px;
        border-radius: 5px;
}
#productList .content .right .store-bar .buytime {
        font-size: 14px;
        color: #666666;
        margin-left: 25px;
}
#productList #oftenbuyList li {
  height: 340px;
}
#productList #oftenbuyList .pro-ms {
  margin-top: 5px;
}
#productList #oftenbuyList .item .pro-btns {
  border: none;
  border-radius: 0;
  background: #F9F9F9;
  color: #666;
  height: 36px;
  width: 100%;
  margin-top: 12px;
}
#productList #oftenbuyList .item .pro-btns img {
    margin-right: 7px;
}
