#shopcar-modal .pttext {
  color: #9999;
  font-size: 12px;
}
#shopcar-modal .collection-item {
  margin-bottom: 10px;
}
#shopcar-modal .collection-item .scbtn {
    border: 1px solid #ddd;
    padding: 3px 10px;
    cursor: pointer;
}
#shopcar-modal .collection-item .scbtn img {
      margin-right: 5px;
}
#shopcar-modal .total {
  margin: 20px 0 28px;
  color: #ff541f;
  font-size: 14px;
}
#shopcar-modal .total ul {
    margin: 0px 10px;
    border: 1px solid #dddddd;
    border-bottom: none;
}
#shopcar-modal .total ul li {
      min-height: 40px;
      border-bottom: 1px solid #dddddd;
}
#shopcar-modal .total ul li div {
        width: 114px;
        text-align: center;
}
